import { Injectable } from '@angular/core';
import { IViewActionService } from '@helix/platform/view/api';
import { isFunction } from 'lodash';
import { Observable, throwError } from 'rxjs';
import { IRefreshViewActionParams } from './refresh-view-action-params.interface';
import { RxError } from '@helix/platform/utils';

@Injectable({
  providedIn: 'root'
})
export class RxRefreshViewActionService implements IViewActionService<IRefreshViewActionParams, never> {
  execute(params: IRefreshViewActionParams): Observable<never> {
    if (params.component) {
      return isFunction(params.component.refresh)
        ? params.component.refresh()
        : throwError(new RxError('rxRefreshAction: target component does not support refresh API.'));
    } else {
      return throwError(new RxError('rxRefreshAction: component is not specified.'));
    }
  }
}
